export class ProductGroupViewController extends BaseTreeViewController {
	constructor( options = {} ) {
		_.defaults( options, {
			el: '#product_group_view_container',

			tree_mode: null,
			grid_table_name: null,
			grid_select_id_array: null
		} );

		super( options );
	}

	init( options ) {
		//this._super('initialize', options );
		this.edit_view_tpl = 'ProductGroupEditView.html';
		this.permission_id = 'product';
		this.viewId = 'ProductGroup';
		this.script_name = 'ProductGroupView';
		this.table_name_key = 'product_group';
		this.context_menu_name = $.i18n._( 'Product Groups' );
		this.grid_table_name = $.i18n._( 'Product Group' );
		this.navigation_label = $.i18n._( 'Product Group' );

		this.tree_mode = true;
		this.primary_tab_label = $.i18n._( 'Product Group' );
		this.primary_tab_key = 'tab_product_group';

		this.api = TTAPI.APIProductGroup;
		this.grid_select_id_array = [];

		this.render();
		this.buildContextMenu();
		this.initData();
	}

	getCustomContextMenuModel() {
		var context_menu_model = {
			exclude: [
				'copy',
				'mass_edit',
				'delete_and_next',
				'save_and_continue',
				'save_and_next',
				'export_excel'
			],
			include: []
		};

		return context_menu_model;
	}

}